#home-box {
    position: relative;
    background-image: linear-gradient(45deg, rgba(255, 216, 155, .8), rgba(25, 84, 123, .8));
    transform: skewY(0deg);
    z-index: -2;
} 
#home-content { 	
    position: relative;
	max-width: 75em;
    margin: 0 auto; 
    transform: skewY(0deg);
    /* background: url('./../../images/logoOnly.png'); */
    background-attachment: fixed;
    background-size: 33%;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 2;
    margin: 5% auto;
    padding: auto;
    
}

#bio-box {
    background-color: rgba(90, 90, 90, 0.3);
    height: 100%;
}

#bio {
    vertical-align: auto;
}

@media only screen and (max-width: 769px){
    #bio-pic{
        width: 50%;
    }
    #bio{
        font-size: 1.1em !important;
    }
}
